<template>
    <div>
        <div class="header">
            <span style="display: flex;align-items: center">
                <i class="ri-music-line" style="margin-right:5px"></i>
                {{nowLyric}}
            </span>
            <span>{{datetime}}</span>
        </div>
        <el-carousel :interval="4000" type="card" height="200px">
            <el-carousel-item v-for="item in 6" :key="item">
                <h3 text="2xl" justify="center">{{ item }}</h3>
            </el-carousel-item>
        </el-carousel>
        <br />
        <el-skeleton :rows="5" animated />
        <br />
        <el-skeleton :rows="5" animated />
    </div>
</template>

<script setup>
import { ref, computed, onMounted } from "vue";
import { useStore } from "vuex";

const store = useStore();

const nowLyric = computed(() => {
    return store.state.nowLyric;
});

const datetime = ref("");
onMounted(() => {
    datetime.value = new Date().toLocaleString();
    setInterval(() => {
        datetime.value = new Date().toLocaleString();
    }, 1000);
});
</script>

<style lang="less" scoped>
.header {
    display: flex;
    flex: 1;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    font-size: 15px;
    color: #999;
    cursor: default;
}
.el-carousel__item {
    h3 {
        color: #475669;
        opacity: 0.75;
        line-height: 200px;
        margin: 0;
        text-align: center;
    }
    &:nth-child(2n) {
        background-color: #99a9bf;
    }
    &:nth-child(2n + 1) {
        background-color: #d3dce6;
    }
}
</style>
